import { mapGetters , mapActions } from 'vuex';

export default {
	data() {
		return {
			sizeArr: [{
				name: '规格',
				childlist: [{
					size: '大杯',
					isSelect: false
				}, {
					size: '中杯',
					isSelect: false
				}]
			}, {
				name: '温度',
				childlist: [{
					size: '冰',
					isSelect: false
				}, {
					size: '热',
					isSelect: false
				}]
			}, {
				name: '糖度',
				childlist: [{
					size: '全糖',
					isSelect: false
				}, {
					size: '半糖',
					isSelect: false
				}, {
					size: '无糖',
					isSelect: false
				}]
			}],
			
			price: 27, //产品单价
			totalPrice: 0,//
			count: 1, //默认1
			sizeStr: ''
		}
	},
	computed: {
		CustomBarH() {
			let sys = uni.getSystemInfoSync()
			return this.CustomBar + (sys.windowWidth / 750 * 30)
		}
	},
	created() {
		this.totalPrice = this.price * this.count
	},
	methods: {
		toCloseBox() {
			this.$emit('toCloseBox', false)
		},
		toSelectProSize(pIdx, cIdx) {
			this.sizeArr.map((item, idx) => {
				if(pIdx == idx) {
					item.childlist.map((v, i) => {
						if(cIdx == i) {
							v.isSelect = true
						}else {
							v.isSelect = false
						}
					})
				}
			})
			this.sizeStr = ''
			this.sizeArr.map((item, idx) => {
				item.childlist.map((v, i) => {
					if(v.isSelect) {
						this.sizeStr += `+${v.size}`
					}
				})
			})
		},
		toMinusNum: function() {
		    if (this.count > 1) {
				this.totalPrice = this.$api.pointProblem(this.totalPrice, this.price, 'sub')
				this.count--
		    }
		},
		toAddNum: function() {
			this.count++
			this.totalPrice = this.$api.pointProblem(this.totalPrice, this.price, 'add')
		}
	}
}
